@mixin center ($width) {
  max-width: $width;
  margin-left: auto;
  margin-right: auto;
}

@mixin font-roboto {
  font-family: "Roboto Condensed", "Arial", sans-serif;
}

@mixin visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
